import axios from 'axios'
import { Toast } from 'vant'

const getQueryString = (key) => {
  function getHashParameters () {
    if ((window.location.hash || '').split('?')[1]) {
      let arr = (window.location.hash || '').split('?')[1].split('&')
      let params = {}
      for (let i = 0; i < arr.length; i++) {
        let data = arr[i].split('=')
        if (data.length === 2) {
          params[data[0]] = data[1]
        }
      }
      return params
    } else {
      return null
    }
  }
  let params = getHashParameters()
  if (params) {
    return params[key]
  } else {
    return ''
  }
}

// pages/goods_list/goods_list?sid=52&title=

const getSidFromUrl = (name, url) => {
  let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
  let sid = url.substr(url.indexOf('?') + 1).match(reg)
  if (sid != null) {
    return unescape(sid[2])
  }
  return null
}

const isWeiXin = () => {
  let ua = window.navigator.userAgent.toLowerCase()
  return ua.match(/MicroMessenger/i) == 'micromessenger'
}

// wx bridgeReady
const onBridgeReady = (response) => {
  let outTradeNo = response.data.outTradeNo
  WeixinJSBridge.invoke(
    "getBrandWCPayRequest",
    response.data,
    function (res) {
      try {
        WeixinJSBridge.log(res.err_msg)
        if (res.err_msg === 'get_brand_wcpay_request:ok') {
          // 
          // router.push('submit-order/pay-result?outTradeNo=' + outTradeNo) //  vue router
          window.location.href = 'https://shop.power-travel.com.cn/dist/index.html#/submit-order/pay-result?outTradeNo=' + outTradeNo //  window location
          // 
        } else {
          if (res.err_msg === 'get_brand_wcpay_request:fail') {
            Toast.fail(' ')
          }
          if (res.err_msg === 'get_brand_wcpay_request:cancel') {
            Toast.fail('')
          }
        }
      } catch (e) {
        console.log(JSON.stringify(e.message))
      }
    }
  )
}

//  
// TODO openid
const wxPay = (params, onBridgeReady) => {
  axios
    .post('/api/Order/pay', params)
    .then(res => {
      if (typeof WeixinJSBridge === 'undefined') {
        if (document.addEventListener) {
          document.addEventListener('WeixinJSBridgeReady', this.onBridgeReady, false)
        } else if (document.attachEvent) {
          document.attachEvent('WeixinJSBridgeReady', this.onBridgeReady)
          document.attachEvent('onWeixinJSBridgeReady', this.onBridgeReady)
        }
      } else {
        onBridgeReady(res.data)
      }
    })
    .catch(error => {
      console.log(JSON.stringify(error))
    })
}

//  h5
const h5Pay = (params, callback, redirectUrl) => {
  console.log('H5')
  axios
    .post('/api/Order/pay', params)
    .then(res => {
      let outTradeNo = res.data.data.outTradeNo
      console.log(outTradeNo)
      console.log(JSON.stringify(res.data))
      let redirectUrl = '/submit-order/pay-result'
      let mwebUrl = res.data.data.mweb_url + '&redirect_url=' + encodeURIComponent(redirectUrl + '?outTradeNo=' + outTradeNo)
      let form = document.createElement('FORM')
      document.body.appendChild(form)
      form.setAttribute('method', 'POST')
      form.setAttribute('action', mwebUrl)
      form.submit()
    })
    .catch(error => {
      console.log(JSON.stringify(error))
    })
}

export {
  getQueryString,
  getSidFromUrl,
  isWeiXin,
  onBridgeReady,
  wxPay,
  h5Pay
}



